body{
  font-family: 'Courier New', Courier, monospace;
  background-color: rgb(222, 222, 222);
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  justify-content: center;
}
.container{
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 2px 2px 1px rgba(4, 4, 4, 0.405);
  padding: 1rem;
  border-radius: 1rem;
}
.time-container{
  color: rgba(0, 0, 0, 0.718);
}
.button-container{
  padding: 1rem;
}
.start-button{
  margin: 0rem 0.4rem;
  padding: 0.5rem 1rem;
  border: none;
  background-color: rgb(103, 202, 63);
  border-radius: 1rem;
  font-weight: bold;
  color: black;
  cursor: pointer;
  transition: 0.5s;
}
.start-button:hover{
   background-color: rgb(31, 53, 22);  
   color: white;
}
.start-button:active{
   background-color: rgb(185, 255, 157);  
   color: black;
}
.stop-button{
  margin: 0rem 0.4rem;
  padding: 0.5rem 1rem;
  border: none;
  background-color: rgb(230, 56, 56);
  border-radius: 1rem;
  font-weight: bold;
  color: black;
  cursor: pointer;
  transition: 0.5s;
}
.stop-button:hover{
  background-color: rgb(87, 17, 17); 
   color: white;
}
.stop-button:active{
    background-color: rgb(255, 120, 120);
   color: black;
}
.reset-button{
  margin: 0rem 0.4rem;
  padding: 0.5rem 1rem;
  border: none;
  background-color: rgb(255, 171, 61);
  border-radius: 1rem;
  font-weight: bold;
  color: rgb(0, 0, 0);
  cursor: pointer;
  transition: 0.5s;
}
.reset-button:hover{
 background-color: rgb(94, 61, 17);
 color: white; 
}
.reset-button:active{
 background-color: rgb(255, 171, 61);
   color: black;
}